<template>
  <div>
    <h1>GPA Calculator</h1>
    <p>For Bachelor of science in information technology (SLIIT)</p>

    <!-- Module Dropdown -->
    <a-select
      show-search
      placeholder="Select a module"
      option-filter-prop="children"
      style="width: 100%"
      :filter-option="filterOption"
      @focus="handleFocus"
      @blur="handleBlur"
      @change="handleChange"
    >
      <!-- Module Listing -->
      <a-select-option v-for="(m, index) in allModules" :key="index" v-bind="m">
        {{ m.name }}
      </a-select-option>
    </a-select>

    <!-- Grade Dropdown -->
    <a-select
      show-search
      placeholder="Grade"
      option-filter-prop="children"
      style="width: 100px; margin: 10px 0 10px 0"
      :filter-option="filterOptionGrade"
      @focus="handleFocus"
      @blur="handleBlur"
      @change="handleChangeGrade"
    >
      <!-- Grade Listing -->
      <a-select-option
        v-for="(grade, index) in grades"
        :key="index"
        v-bind="grade"
      >
        {{ grade }}
      </a-select-option>
    </a-select>

    <!-- Add to List Button -->
    <a-button
      type="primary"
      style="margin: 0 10px 0 10px"
      v-on:click="addToTable"
    >
      Add to List
    </a-button>

    <!-- Add to List Button -->
    <a-button type="danger" style="margin: 0 10px 0 0" v-on:click="clearTable">
      Clear Table
    </a-button>

    <a-alert
      v-if="err"
      message="Error"
      description="You must fill all inputs to add to the table!"
      type="error"
      banner="true"
      show-icon
      style="margin: 10px 0 10px 0;"
    />

    <!-- Table -->
    <a-table bordered :data-source="modules" :columns="columns">
      <template slot="name" slot-scope="text, record">
        <editable-cell
          :text="text"
          @change="onCellChange(record.key, 'name', $event)"
        />
      </template>
      <template slot="operation" slot-scope="text, record">
        <a-popconfirm
          v-if="modules.length"
          title="Sure to delete?"
          @confirm="() => onDelete(record.key)"
        >
          <a href="javascript:;">Delete</a>
        </a-popconfirm>
      </template>
    </a-table>

    <!-- Calculate Button -->
    <a-button
      type="primary"
      style="margin: 10px 0 10px 0"
      v-on:click="calculateGPA"
    >
      Calculate GPA
    </a-button>

    <!-- Result Show -->
    <template>
      <a-row :gutter="16">
        <a-col :span="12">
          <a-statistic title="GPA" :value="cgpa" style="margin-right: 50px">
            <template #suffix>
              <a-icon type="book" />
            </template>
          </a-statistic>
        </a-col>
      </a-row>
    </template>
  </div>
</template>

<script>
import ModulesJSON from "../data/modules.json";

export default {
  data() {
    return {
      err: false,
      mods: [],
      cgpa: "",
      totalG: "",
      totalC: "",
      // Form Data
      formItems: {
        name: "",
        grade: "",
        credit: "",
        gpa: "",
      },
      //   Column Headers
      columns: [
        {
          title: "Module",
          dataIndex: "name",
          key: "name",
        },
        {
          title: "Credit",
          dataIndex: "credit",
          key: "age",
          width: 80,
        },
        {
          title: "Grade",
          dataIndex: "grade",
          key: "address 1",
          width: 80,
          ellipsis: true,
        },
        {
          title: "GPA",
          dataIndex: "gpa",
          key: "address 1",
          width: 80,
          ellipsis: true,
        },
      ],
      //   Data
      modules: [],
      grades: [
        "A+",
        "A",
        "A-",
        "B+",
        "B",
        "B-",
        "C+",
        "C",
        "C-",
        "D+",
        "D",
        "E",
      ],
      allModules: ModulesJSON.modules,
    };
  },
  created() {
    this.$http
      .get(
        "https://gpa-calculator-ed3e7-default-rtdb.firebaseio.com/modules.json"
      )
      .then(function (data) {
        return data.json();
      })
      .then(function (data) {
        console.log(data);
        this.allModules = data;
      });
  },
  methods: {
    handleChange(value) {
      console.log(`selected ${value}`);
      this.formItems.name = this.allModules[value].name;
      this.formItems.credit = this.allModules[value].credit;
    },
    handleChangeGrade(value) {
      console.log(`selected ${value}`);
      this.formItems.grade = this.grades[value];
      let temp;
      switch (this.grades[value]) {
        case "A+":
          temp = 4.0;
          break;
        case "A":
          temp = 4.0;
          break;
        case "A-":
          temp = 3.7;
          break;
        case "B+":
          temp = 3.3;
          break;
        case "B":
          temp = 3.0;
          break;
        case "B-":
          temp = 2.7;
          break;
        case "C+":
          temp = 2.3;
          break;
        case "C":
          temp = 2.0;
          break;
        case "C-":
          temp = 1.7;
          break;
        case "D+":
          temp = 1.3;
          break;
        case "D":
          temp = 1.0;
          break;
        case "E":
          temp = 0.0;
          break;
        default:
          temp = 0.0;
      }
      this.formItems.gpa = temp;
    },
    handleBlur() {
      console.log("blur");
    },
    handleFocus() {
      console.log("focus");
    },
    filterOption(input, option) {
      const ret =
        option.componentOptions.children[0].text
          .toLowerCase()
          .indexOf(input.toLowerCase()) >= 0;
      return ret;
    },
    filterOptionGrade(input, option) {
      const ret =
        option.componentOptions.children[0].text
          .toLowerCase()
          .indexOf(input.toLowerCase()) >= 0;
      return ret;
    },
    addToTable() {
      const val = {
        name: this.formItems.name,
        grade: this.formItems.grade,
        credit: this.formItems.credit,
        gpa: this.formItems.gpa,
      };
      if (val.name == "" || val.grade == "") {
        this.err = true
      } else {
        this.modules.push(val);
        this.err = false
      }
    },
    calculateGPA() {
      let totalGPA = 0;
      let totalCredit = 0;
      this.modules.forEach((e) => {
        totalGPA += e.gpa * Number(e.credit);
        totalCredit += Number(e.credit);
      });
      this.totalG = totalGPA;
      this.totalC = totalCredit;
      this.cgpa = totalGPA / totalCredit;
      console.log(totalGPA);
    },
    clearTable() {
      this.modules = [];
      this.err = false
    },
  },
};
</script>
